import React from "react";
import classes from "./comment.module.css";
import fire from "../../firebase/firebase";

const Comment = (props) => {
  // console.log(fire.database());

  return (
    <div className={classes.msg}>
      <p>{props.msg}</p>
      <span> {props.date} </span>
      <span> UNKNOWN </span>
    </div>
  );
};

export default Comment;
